<template>
	<v-container fill-height align-center justify-center class="px-0">
		<v-row>
			<v-col cols="12">
				<video-container v-if="isStarted" @goBack="stopStream" @countdownExpired="switchMusic"/>
				<welcome-panel v-else @startStream="startStream" @modelLoaded="$emit('dataLoaded')"/>
			</v-col>
		</v-row>
		<audio src="../assets/music/christmas_calm_music.mp3" id="music-before" loop></audio>
		<audio src="../assets/music/christmas_happy_music.mp3" id="music-after" loop></audio>
	</v-container>
</template>

<script>
	import VideoContainer from "../components/VideoContainer";
	import CountDown from "../components/CountDown";
	import WelcomePanel from "../components/WelcomePanel";
	import ImageGallery from "../components/ImageGallery";
	import ModelViewer from "../components/ModelViewer";
	export default {
		name: "MainPage",
		components: {ModelViewer, ImageGallery, WelcomePanel, CountDown, VideoContainer},
		data() {
			return {
				isStarted: false,
				isTimerExpired: false,
				musicEnabled: false,
				lorem: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.'
			}
		},
		methods: {
			startStream() {
				const vm = this;

				vm.$emit('dataLoaded');

				vm.isStarted = true;

				if (vm.musicEnabled) document.getElementById(vm.isTimerExpired ? 'music-after' : 'music-before').play();
			},
			stopStream() {
				const vm = this;

				vm.$emit('dataLoading');

				vm.isStarted = false;

				if (vm.musicEnabled) document.getElementById(vm.isTimerExpired ? 'music-after' : 'music-before').pause();
			},
			switchMusic() {
				const vm = this;

				vm.isTimerExpired = true;

				if (vm.isStarted) {
					if (vm.musicEnabled) document.getElementById('music-before').pause();
					if (vm.musicEnabled) document.getElementById('music-after').play();
				}
			}
		}
	}
</script>

<style scoped lang="scss">
	p {
		text-align: center;
	}

	.content-container {
		height: 100%;
	}

	.col-wrapper {
		align-items: center;
		text-align: center;
		display: flex;
		justify-content: center;

		&.tight {

		}
	}

	.disable-interaction {
		pointer-events: none !important;
	}
</style>
