<template>
	<!--<model-fbx :width="850" :height="850" src="models/nasaHisa.fbx" :glOptions="options" :backgroundAlpha="0"></model-fbx>-->
	<model-gltf :width="modelSize.w" :height="modelSize.h" :src="modelUrl" :glOptions="options" :rotation="rotation"
				:backgroundAlpha="0" @on-load="onLoad"></model-gltf>
</template>

<script>
	import {ModelFbx, ModelGltf} from 'vue-3d-model';

	export default {
		name: "ModelViewer",
		data: () => ({
			modelUrl: 'https://www.dropbox.com/s/xr9b6kftk6az36e/nasaHisa.glb?dl=1',
			options: {
				antialias: true,
				alpha: true,
				precision: "highp",
				powerPreference: "high-performance",
				logarithmicDepthBuffer: true,
			},
			rotation: {
				x: 0,
				y: 0,
				z: 0
			}
		}),
		components: {ModelFbx, ModelGltf},
		created() {
			const vm = this;


		},
		computed: {
			modelSize() {
				const vm = this;
				switch (this.$vuetify.breakpoint.name) {
					case 'xs':
					case 'sm':
						return {
							w: 340,
							h: 340
						};
						break;
					case 'md':
						return {
							w: 500,
							h: 500
						};
						break;
					case 'lg':
					case 'xl':
					default:
						return {
							w: 850,
							h: 850
						};
						break;
				}
			}
		},
		methods: {
			onLoad() {
				this.$emit('loaded');
				this.rotate();
			},
			rotate() {
				this.rotation.y += 0.01;
				requestAnimationFrame(this.rotate);
			}
		}
	}
</script>

